import { Point } from '../primitives/point';
import { Rect } from '../primitives/rect';
import { intersect2 } from '../utility/diagram-util';
import { canBridge } from '../utility/constraints-util';
/**
 * ConnectorBridging defines the bridging behavior
 */
/** @private */
export class ConnectorBridging {
    /** @private */
    updateBridging(conn, diagram) {
        let lastBridge = [];
        let bounds;
        conn.bridges = [];
        if (canBridge(conn, diagram)) {
            // if (this.canBridge(conn, diagram)) {
            let points1 = this.getPoints(conn);
            bounds = Rect.toBounds(points1);
            let bridgeSpacing = conn.bridgeSpace;
            let bgedir = diagram.bridgeDirection;
            let count = -1;
            let quads = diagram.connectors;
            for (let q = 0; q < quads.length; q++) {
                let connector1 = quads[q];
                if (conn && connector1 && conn.id !== connector1.id) {
                    let points2 = this.getPoints(connector1);
                    let bounds1 = Rect.toBounds(points2);
                    if (this.intersectsRect(bounds, bounds1)) {
                        let intersectPts = this.intersect(points1, points2, false, bgedir, true);
                        if (intersectPts.length > 0) {
                            for (let i = 0; i < intersectPts.length; i++) {
                                let fullLength = 0;
                                let length = 0;
                                let segmentIndex = 0;
                                let pointIndex = 0;
                                let obj = this.getLengthAtFractionPoint(conn, intersectPts[i]);
                                if (obj.pointIndex !== -1) {
                                    length = obj.lengthFractionIndex;
                                    fullLength = obj.fullLength;
                                    segmentIndex = obj.segmentIndex;
                                    pointIndex = obj.pointIndex;
                                    let stBridge = this.getPointAtLength((length - (bridgeSpacing / 2)), points1);
                                    let enBridge = this.getPointAtLength((length + (bridgeSpacing / 2)), points1);
                                    let fractLength = (length - (bridgeSpacing / 2)) / fullLength;
                                    fractLength = (length + (bridgeSpacing / 2)) / fullLength;
                                    if (this.isEmptyPoint(enBridge)) {
                                        enBridge = stBridge;
                                    }
                                    let start;
                                    let end;
                                    start = conn.sourcePoint;
                                    if (conn.type === 'Straight') {
                                        end = conn.targetPoint;
                                    }
                                    else {
                                        end = conn.intermediatePoints[pointIndex];
                                    }
                                    let angle = this.angleCalculation(start, end);
                                    if (lastBridge.length) {
                                        let fixedPoint;
                                        fixedPoint = conn.sourcePoint;
                                        let fix = Math.abs(this.lengthCalculation(fixedPoint, enBridge));
                                        let var1 = 0;
                                        let insertAt = -1;
                                        count = -1;
                                        for (let k = 0; k < lastBridge[segmentIndex].bridges.length; k++) {
                                            count++;
                                            let arcSeg = lastBridge[segmentIndex].bridges[k];
                                            var1 = Math.abs(this.lengthCalculation(fixedPoint, arcSeg.endPoint));
                                            if (fix < var1) {
                                                insertAt = count;
                                                break;
                                            }
                                        }
                                        if (insertAt >= 0) {
                                            let paths;
                                            paths = this.createSegment(stBridge, enBridge, angle, bgedir, pointIndex, conn, diagram);
                                            paths.target = connector1.id;
                                            lastBridge[segmentIndex].bridges.splice(insertAt, 0, paths);
                                            lastBridge[segmentIndex].bridges.join();
                                            lastBridge[segmentIndex].bridgeStartPoint.splice(insertAt, 0, stBridge);
                                            lastBridge[segmentIndex].bridgeStartPoint.join();
                                            lastBridge[segmentIndex].segmentIndex = segmentIndex;
                                        }
                                        else {
                                            let paths;
                                            paths = this.createSegment(stBridge, enBridge, angle, bgedir, pointIndex, conn, diagram);
                                            paths.target = connector1.id;
                                            lastBridge[segmentIndex].bridges.push(paths);
                                            lastBridge[segmentIndex].bridgeStartPoint.push(stBridge);
                                            lastBridge[segmentIndex].segmentIndex = segmentIndex;
                                        }
                                    }
                                    else {
                                        if (!isNaN(stBridge.x) && !isNaN(stBridge.y) && !this.isEmptyPoint(enBridge)) {
                                            let arcs;
                                            let bges = [];
                                            let bgept = [];
                                            arcs = this.createSegment(stBridge, enBridge, angle, bgedir, pointIndex, conn, diagram);
                                            let bgseg = {
                                                bridges: bges, bridgeStartPoint: bgept, segmentIndex: segmentIndex
                                            };
                                            arcs.target = connector1.id;
                                            let stPoints = [];
                                            let edPoints = [];
                                            stPoints.push(stBridge);
                                            edPoints.push(enBridge);
                                            lastBridge[segmentIndex] = bgseg;
                                            lastBridge[segmentIndex].bridges.push(arcs);
                                            lastBridge[segmentIndex].bridgeStartPoint = stPoints;
                                            lastBridge[segmentIndex].segmentIndex = segmentIndex;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            if (lastBridge.length !== 0) {
                this.firstBridge(lastBridge, conn, bridgeSpacing);
            }
        }
    }
    /** @private */
    firstBridge(bridgeList, connector, bridgeSpacing) {
        for (let i = 0; i < bridgeList.length; i++) {
            let bridge = bridgeList[i];
            for (let k = 1; k < bridge.bridges.length; k++) {
                if (Point.findLength(bridge.bridges[k].endPoint, bridge.bridges[k - 1].endPoint) < bridgeSpacing) {
                    bridge.bridges[k - 1].endPoint = bridge.bridges[k].endPoint;
                    let subBridge = bridge.bridges[k - 1];
                    let arc = this.createBridgeSegment(subBridge.startPoint, subBridge.endPoint, subBridge.angle, bridgeSpacing, subBridge.sweep);
                    bridge.bridges[k - 1].path = arc;
                    bridge.bridges.splice(k, 1);
                    bridge.bridgeStartPoint.splice(k, 1);
                    k--;
                }
            }
            let pre = connector.sourcePoint;
            for (let j = 0; j < bridge.bridges.length; j++) {
                let subBridge = bridge.bridges[j];
                let preventChecking = true;
                pre = subBridge.endPoint;
                connector.bridges.push(subBridge);
            }
        }
    }
    /** @private */
    createSegment(st, end, angle, direction, index, conn, diagram) {
        let arc;
        let sweep;
        let path = {
            angle: 0, endPoint: { x: 0, y: 0 }, target: '', path: '',
            segmentPointIndex: -1, startPoint: { x: 0, y: 0 }, sweep: 1, rendered: false
        };
        sweep = this.sweepDirection(angle, direction, conn, diagram);
        arc = this.createBridgeSegment(st, end, angle, conn.bridgeSpace, sweep);
        path.path = arc;
        path.startPoint = st;
        path.endPoint = end;
        path.angle = angle;
        path.segmentPointIndex = index;
        path.sweep = sweep;
        return path;
    }
    /** @private */
    createBridgeSegment(startPt, endPt, angle, bridgeSpace, sweep) {
        let path = 'A ' + bridgeSpace / 2 + ' ' + bridgeSpace / 2 + ' ' + angle + ' , 1 ' + sweep + ' ' + endPt.x + ',' + endPt.y;
        return path;
    }
    /** @private */
    sweepDirection(angle, bridgeDirection, connector, diagram) {
        let angle1 = Math.abs(angle);
        let sweep;
        switch (bridgeDirection) {
            case 'Top':
            case 'Bottom':
                sweep = 1;
                if (angle1 >= 0 && angle1 <= 90) {
                    sweep = 0;
                }
                break;
            case 'Left':
            case 'Right':
                sweep = 1;
                if (angle < 0 && angle >= -180) {
                    sweep = 0;
                }
                break;
        }
        if (bridgeDirection === 'Right' || bridgeDirection === 'Bottom') {
            if (sweep === 0) {
                sweep = 1;
            }
            else {
                sweep = 0;
            }
        }
        return sweep;
    }
    /** @private */
    getPointAtLength(length, pts) {
        let run = 0;
        let pre;
        let found = { x: 0, y: 0 };
        for (let i = 0; i < pts.length; i++) {
            let pt = pts[i];
            if (!pre) {
                pre = pt;
                continue;
            }
            else {
                let l = this.lengthCalculation(pre, pt);
                if (run + l > length) {
                    let r = length - run;
                    let deg = Point.findAngle(pre, pt);
                    let x = r * Math.cos(deg * Math.PI / 180);
                    let y = r * Math.sin(deg * Math.PI / 180);
                    found = { x: pre.x + x, y: pre.y + y };
                    break;
                }
                else {
                    run += l;
                }
            }
            pre = pt;
        }
        return found;
    }
    /** @private */
    getPoints(connector) {
        let points = [];
        if (connector.intermediatePoints && (connector.type === 'Straight' || connector.type === 'Orthogonal')) {
            for (let j = 0; j < connector.intermediatePoints.length; j++) {
                points.push(connector.intermediatePoints[j]);
            }
        }
        return points;
    }
    intersectsRect(rect1, rect2) {
        return ((((rect2.x < (rect1.x + rect1.width)) && (rect1.x < (rect2.x + rect2.width)))
            && (rect2.y < (rect1.y + rect1.height))) && (rect1.y < (rect2.y + rect2.height)));
    }
    /** @private */
    intersect(points1, points2, self, bridgeDirection, zOrder) {
        if (self && points2.length >= 2) {
            points2.splice(0, 1);
            points2.splice(0, 1);
        }
        let points = [];
        for (let i = 0; i < points1.length - 1; i++) {
            let pt = this.inter1(points1[i], points1[i + 1], points2, zOrder, bridgeDirection);
            if (pt.length > 0) {
                for (let k = 0; k < pt.length; k++) {
                    points.push(pt[k]);
                }
            }
            if (self && points2.length >= 1) {
                points2.splice(0, 1);
            }
        }
        return points;
    }
    /** @private */
    inter1(startPt, endPt, pts, zOrder, bridgeDirection) {
        let points1 = [];
        for (let i = 0; i < pts.length - 1; i++) {
            let point = intersect2(startPt, endPt, pts[i], pts[i + 1]);
            if (!this.isEmptyPoint(point)) {
                let angle = this.angleCalculation(startPt, endPt);
                let angle1 = this.angleCalculation(pts[i], pts[i + 1]);
                angle = this.checkForHorizontalLine(angle);
                angle1 = this.checkForHorizontalLine(angle1);
                switch (bridgeDirection) {
                    case 'Left':
                    case 'Right':
                        if (angle > angle1) {
                            points1.push(point);
                        }
                        break;
                    case 'Top':
                    case 'Bottom':
                        if (angle < angle1) {
                            points1.push(point);
                        }
                        break;
                }
                if (angle === angle1 && zOrder) {
                    points1.push(point);
                }
            }
        }
        return points1;
    }
    checkForHorizontalLine(angle) {
        let temp = 0;
        let roundedAngle = Math.abs(angle);
        if (roundedAngle > 90) {
            temp = 180 - roundedAngle;
        }
        else {
            temp = roundedAngle;
        }
        return temp;
    }
    isEmptyPoint(point) {
        return point.x === 0 && point.y === 0;
    }
    getLengthAtFractionPoint(connector, pointAt) {
        let lengthFraction;
        let confirm = 100;
        let pointIndex = -1;
        let fullLength = 0;
        let segmentIndex = -1;
        let count = 0;
        let lengthAtFractionPt = 0;
        let pt1 = connector.sourcePoint;
        let previouspt2 = pt1;
        let points = [];
        for (let i = 0; i < connector.intermediatePoints.length; i++) {
            let point2 = connector.intermediatePoints[i];
            points.push(point2);
        }
        for (let j = 0; j < points.length; j++) {
            let pt2 = points[j];
            let suspect = this.getSlope(pt2, pt1, pointAt, connector);
            if (suspect < confirm) {
                confirm = suspect;
                lengthAtFractionPt = fullLength + this.lengthCalculation(pointAt, previouspt2);
                segmentIndex = count;
                pointIndex = j;
            }
            fullLength += Point.findLength(pt2, pt1);
            pt1 = pt2;
            previouspt2 = pt2;
        }
        count++;
        lengthFraction = {
            lengthFractionIndex: lengthAtFractionPt, fullLength: fullLength,
            segmentIndex: segmentIndex, pointIndex: pointIndex
        };
        return lengthFraction;
    }
    getSlope(startPt, endPt, point, connector) {
        let three = 3.0;
        let delX = Math.abs(startPt.x - endPt.x);
        let delY = Math.abs(startPt.y - endPt.y);
        let lhs = ((point.y - startPt.y) / (endPt.y - startPt.y));
        let rhs = ((point.x - startPt.x) / (endPt.x - startPt.x));
        if (!isFinite(lhs) || !isFinite(rhs) || isNaN(lhs) || isNaN(rhs)) {
            if (startPt.x === endPt.x) {
                if (startPt.y === endPt.y) {
                    return 10000;
                }
                else if (((startPt.y > point.y) && (point.y > endPt.y)) || ((startPt.y < point.y) && (point.y < endPt.y))) {
                    return Math.abs(startPt.x - point.x);
                }
            }
            else if (startPt.y === endPt.y) {
                if (((startPt.x > point.x) && (point.x > endPt.x)) || ((startPt.x < point.x) && (point.x < endPt.x))) {
                    return Math.abs(startPt.y - point.y);
                }
            }
        }
        else {
            if ((startPt.x >= point.x && point.x >= endPt.x) || (startPt.x <= point.x && point.x <= endPt.x) || delX < three) {
                if ((startPt.y >= point.y && point.y >= endPt.y) || (startPt.y <= point.y && point.y <= endPt.y) || delY < three) {
                    return Math.abs(lhs - rhs);
                }
            }
        }
        return 10000;
    }
    /** @private */
    angleCalculation(startPt, endPt) {
        let xDiff = startPt.x - endPt.x;
        let yDiff = startPt.y - endPt.y;
        return Math.atan2(yDiff, xDiff) * (180 / Math.PI);
    }
    lengthCalculation(startPt, endPt) {
        //removed a try catch from here
        let len = Math.sqrt(((startPt.x - endPt.x) * (startPt.x - endPt.x)) + ((startPt.y - endPt.y) * (startPt.y - endPt.y)));
        return len;
    }
    /**
     * Constructor for the bridging module
     * @private
     */
    constructor() {
        //constructs the bridging module
    }
    /**
     * To destroy the bridging module
     * @return {void}
     * @private
     */
    destroy() {
        /**
         * Destroys the bridging module
         */
    }
    /**
     * Get module name.
     */
    getModuleName() {
        /**
         * Returns the module name
         */
        return 'Bridging';
    }
}
