    /**
     * To destroy the snapping module
     * @return {void}
     * @private
     */
    destroy() {
        /**
         * Destroys the snapping module
         */
    }
    /**
     * Get module name.
     */
    getModuleName() {
        /**
         * Returns the module name
         */
        return 'Snapping';
    }
